import { requestGet, requestPost } from '@engine/crud';

/**
 * 获取超链接图表资源数据
 * @param ChartHyperlink_ID 超链接图表ID
 */
export function fetchChartPop(ChartHyperlink_ID: string, parameters?: { [key: string]: Array<string> | number | string }): Promise<{
    chartHeight: number;
    chartWidth: number;
    isNeedRefreshButton: Boolean;
    selectedIndex: number;
    type: string;
    items: {
        chartImageType: string;
        chartJSDraw: boolean;
        name: string;
        simpleChartInShowID: string;
        simpleIconType: string;
        url: string;
    }[];
}> {
    return requestGet('chart/pop', {
        params: {
            ChartHyperlink_ID,
            __parameters__: parameters,
        },
    });
}

/**
 * 获取公式计算的值
 */
export async function remoteEvaluate(expression: string): Promise<{ result: string } | undefined> {
    return requestPost('/remote/evaluate', {
        data: {
            expression,
        },
    });
}

/**
 * 发送邮件
 */
export function sendEmail(data: {
    [key: string]: any
}): Promise<{
        status: 'error'|'success'
    }> {
    return requestPost('email/send', {
        data,
    });
}
